import axios from '@nextcloud/axios';
import OAuthItem from "./components/OAuthItem.vue";
import { generateUrl } from '@nextcloud/router';
import { getCapabilities } from '@nextcloud/capabilities';
import NcSettingsSection from '@nextcloud/vue/components/NcSettingsSection';
import NcButton from '@nextcloud/vue/components/NcButton';
import NcNoteCard from '@nextcloud/vue/components/NcNoteCard';
import { loadState } from '@nextcloud/initial-state';
import NcTextField from '@nextcloud/vue/components/NcTextField';
export default {
  name: 'App',
  components: {
    OAuthItem,
    NcSettingsSection,
    NcButton,
    NcTextField,
    NcNoteCard
  },
  props: {
    clients: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      newClient: {
        name: '',
        redirectUri: '',
        errorMsg: '',
        error: false
      },
      oauthDocLink: loadState('oauth2', 'oauth2-doc-link'),
      showSecretWarning: false
    };
  },
  computed: {
    instanceName() {
      return getCapabilities().theming.name;
    }
  },
  methods: {
    deleteClient(id) {
      axios.delete(generateUrl('apps/oauth2/clients/{id}', {
        id
      })).then(() => {
        // eslint-disable-next-line vue/no-mutating-props
        this.clients = this.clients.filter(client => client.id !== id);
      });
    },
    addClient() {
      this.newClient.error = false;
      axios.post(generateUrl('apps/oauth2/clients'), {
        name: this.newClient.name,
        redirectUri: this.newClient.redirectUri
      }).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.clients.push(response.data);
        this.showSecretWarning = true;
        this.newClient.name = '';
        this.newClient.redirectUri = '';
      }).catch(reason => {
        this.newClient.error = true;
        this.newClient.errorMsg = reason.response.data.message;
      });
    }
  }
};